.img-container {
  width: 350px;
  border: 2px solid #000;
  border-radius: 15px;
  margin: 0 auto;
  overflow: hidden;
}

hr {
  color: #ff0;
  text-align: center;
  height: 5px;
  border: none;
  border-top: 3px double #fff;
  margin: 15px auto;
  overflow: visible;
}

hr:after {
  background: var(--main-container-bottom-backcolor);
  padding: 0 4px;
  position: relative;
  top: -13px;
}

.hr-start:after {
  content: "Przykład - początek";
}

.hr-end:after {
  content: "Przykład - koniec";
}

.code-display {
  white-space: pre-wrap;
  overflow-wrap: break-word;
}

